<template>
  <div class=" mt-28 lg:px-20 px-8">
      <h1 class=" font-mono text-4xl">Resume</h1>
      
      <section class=" mt-6 space-y-6 font-mono prose lg:prose-md">
        <content-loader v-if="!ready">
          <rect x="0" y="13" rx="4" ry="4" width="400" height="9" />
    <rect x="0" y="29" rx="4" ry="4" width="100" height="8" />
    <rect x="0" y="50" rx="4" ry="4" width="400" height="10" />
    <rect x="0" y="65" rx="4" ry="4" width="400" height="10" />
    <rect x="0" y="79" rx="4" ry="4" width="100" height="10" />
    <rect x="0" y="99" rx="5" ry="5" width="400" height="200" />
        </content-loader>
           <prismic-rich-text :field="fields.content"/>
      </section>

      <section v-if="ready" class=" relative font-mono py-4 px-6 md:py-8 md:px-12 items-end flex border rounded-lg">
        <div class=" w-full">
          <h2 class=" text-sm font-bold md:font-normal md:text-xl">Appreciate my experience?</h2>
          <h3 class=" text-xs mt-1 md:mt-0  md:text-sm">You can keep a copy of the Resume here!</h3>
          
          <div class=" flex mt-6">

          
          <a :href="fields.pdfDownloadLink" class=" bg-white border w-full md:w-auto text-center border-gray-400 hover:bg-gray-100 text-xs px-4 py-3"><i class='bx bx-link-external' ></i>Download</a>
          </div>
        </div>
        <div  style=" z-index:-1" class=" hidden md:block absolute right-10">
          <i class='bx text-gray-100 text-8xl bxs-file-doc'></i>
        </div>
      </section>

    
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader'
export default {
  components: {
    ContentLoader
  },
  data() {
    return {
      fields: {
        content: [],
        pdfDownloadLink: ''
      },
      ready: false
    }
  },
  methods: {
    getContent() {
      this.$prismic.client.getSingle("resume")
      .then(document => {
        this.fields.content =  document.data["resume-description"]
        this.ready = true
        this.fields.pdfDownloadLink = document.data['pdf_download_link'].url
      })
    }
  },
  created() {
    this.getContent()
  }
}
</script>

